  background: rgba(166, 166, 166, 0.5);
  border-radius: 10px;
}

.choices__item--disabled {
  display: none;
}

/***choices***/
/***filter-box***/
.filter-box {
  padding: 24px;
  border-radius: 8px;
  border: 1px solid #A6A6A6;
  display: flex;
  flex-wrap: wrap;
  gap: 24px 0;
}
.filter-box .input-checkbox {
  width: 50%;
  padding-right: 15px;
}
.filter-box .input-checkbox:after {
  display: none;
}
.filter-box .input-checkbox_box {
  justify-content: flex-start;
}

/***filter-box***/
/***section-account***/
.section-account .account-box {
  background: #ffffff;
}
@media all and (hover: hover) {
  .section-account .account-item:hover, .section-account .account-item:focus-visible {
    background: #F1FBC7;
    border-color: #F1FBC7;
  }
}
.section-account .account-item.active {
  background: #F1FBC7;
}

.account-info {
  border-radius: 32px;
  background: #ffffff;
  padding: 16px;
}
.account-infoItem:not(:last-of-type) {
  padding-bottom: 32px;
  margin-bottom: 32px;
  border-bottom: 1px solid #A6A6A6;
}
.account-infoItem__icon {
  width: 48px;
  height: 48px;
  flex: 0 0 48px;
  padding: 12px;
  border-radius: 8px;
  background: #C3E00D;
  margin-bottom: 16px;
}
.account-infoItem__icon svg {
  max-width: 100%;
  max-height: 100%;
}
.account-infoItem__title {
  font-size: 32px;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.account-infoItem__text {
  color: #708200;
  text-transform: uppercase;
}

#accountMenu {
  width: 316px;
  flex: 0 0 316px;
}
@media (max-width: 991.98px) {
  #accountMenu {
    width: 100%;
    flex: 0 0 100%;
  }
}

.accountBg {
  border-radius: 32px;
  background: #ffffff;
  padding: 32px;
}
@media (max-width: 767.98px) {
  .accountBg {
    padding: 16px;
  }
}

#content-account {
  width: calc(100% - 316px);
  flex: 0 0 calc(100% - 316px);
}
@media (max-width: 991.98px) {
  #content-account {
    width: 100%;
    flex: 0 0 100%;
  }
}

/***section-account***/
/***order-item***/
.order-item_head {
  text-transform: uppercase;
  color: #7E7E7E;
  font-size: 14px;
  margin-bottom: 4px;
}
.order-item_title {
  text-transform: uppercase;
  font-size: 16px;
}
.order-item_title--status_done {
  color: #708200;
}
.order-item_title--status_progress {
  color: #FF922E;
}
.order-item_btn {
  width: 48px;
  height: 48px;
  flex: 0 0 48px;
  border-radius: 6px;
  border: 1px solid #A6A6A6;
  position: relative;
}
.order-item_btn:after {
  content: "";
  position: absolute;
  right: 50%;
  top: 50%;
  margin-top: -2px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #181818;
  transition: all ease-in-out 0.3s;
}
.order-item_btn:not(.collapsed) {
  transform: rotateX(180deg);
}
.order-item_btn:after {
  margin-right: -4px;
  transition: all ease 0.3s;
}

.order-content {
  background: #F1FBC7;
  border-radius: 16px;
  margin-top: 32px;
  padding: 16px;
}
.order-content .compound {
  border-radius: 8px;
  background: #FFE676;
  padding: 8px 16px;
}
@media (max-width: 767.98px) {
  .order-content .compound {
    padding: 8px 10px;
  }
}

.orderCard-items {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 0;
}

/***order-item***/
/***orderProgram***/
.orderProgram-item {
  padding: 24px;
  border-radius: 16px;
}
@media (max-width: 575.98px) {
  .orderProgram-item {
    padding: 16px;
  }
}
.orderProgram-item:not(:last-of-type) {
  margin-bottom: 32px;
}
@media (max-width: 575.98px) {
  .orderProgram-item:not(:last-of-type) {
    margin-bottom: 16px;
  }
}
.orderProgram-item-bg1 {
  background: #F1FBC7;
}
.orderProgram-item-bg2 {
  background: #FFF9E5;
}
.orderProgram-item-bg3 {
  background: #FFF0F2;
}
.orderProgram-item__title {
  font-size: 40px;
  text-transform: uppercase;
}
@media (max-width: 575.98px) {
  .orderProgram-item__title {
    font-size: 36px;
  }
}

/***orderProgram***/
/***order-box***/
.order-box__header {
  border-top: 1px solid rgba(166, 166, 166, 0.5);
  padding-top: 16px;
  margin-top: 16px;
}
.order-box__header--title {
  font-size: 32px;
  text-transform: uppercase;
  margin-bottom: 4px;
}
@media (max-width: 767.98px) {
  .order-box__header--title {
    font-size: 24px;
  }
}
.order-box__header--programs {
  display: flex;
  align-items: center;
  gap: 8px;
}
.order-box__header--programs span {
  font-size: 20px;
  text-transform: uppercase;
}
@media (max-width: 767.98px) {
  .order-box__header--programs span {
    font-size: 16px;
  }
}
.order-box__header--programs_calories {
  border-radius: 6px;
  background: #ffffff;
  padding: 4px 8px;
  text-transform: uppercase;
}
.order-box__header--time {
  text-transform: uppercase;
  font-size: 20px;
  color: #7E7E7E;
}
@media (max-width: 767.98px) {
  .order-box__header--time {
    font-size: 16px;
  }
}
.order-box__header--time span {
  color: #181818;
}
.order-box__counter {
  text-transform: uppercase;
  color: #7E7E7E;
  font-size: 20px;
}
.order-box__counter--current {
  color: #181818;
}
.order-box__counter--all {
  color: #708200;
}
.order-box__dates {
  background: #ffffff;
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 24px;
}
.order-box__footer {
  border-top: 1px solid rgba(166, 166, 166, 0.5);
  padding-top: 24px;
  margin-top: 24px;
}
.order-box__footer .compound {
  padding: 8px 16px;
  border-radius: 8px;
  background: #FFE676;
}
@media (max-width: 767.98px) {
  .order-box__footer .compound {
    padding: 8px 10px;
  }
}

