/***error***/
.section-error {
  margin-bottom: 80px;
}

.error {
  text-align: center;
}
.error__title {
  font-size: clamp(200px, 31.6vw, 400px);
  line-height: 1.1;
  font-weight: 600;
}
.error__text {
  font-size: 24px;
  color: #7E7E7E;
  text-transform: uppercase;
}
@media (max-width: 767.98px) {
  .error__text {
    font-size: 20px;
  }
}
